<template>
    <div>
        <ul class="plugin-list">
            <li>
                <h3>Name</h3>
                <div class="text">Calendar</div>
            </li>
            <li>
                <h3>Introduction</h3>
                <div class="text"></div>
            </li>
            <li>
                <h3>Demo</h3>
                <div id="demoDate">
                    <!---->
                </div>
                <div class="text">
                    <input type="text" readonly="readonly" v-model="nowVal">
                </div>
            </li>
            <li>
                <h3>Usage</h3>
<pre class="JS">
// 
var calendar = new Calendar('#demoDate', {
    // 
    minDate: '2017-02-11',

    // 
    maxDate: '2017-05-11',

    // 
    callBack: function (nowVal) {
        self.nowVal = nowVal;
    }
});
</pre>
            </li>
        </ul>
    </div>
</template>

<script>
    import Calendar from '../../lib/calendar/calendar'

	export default {
		name: 'calendar',
        data(){
            return {
                nowVal: ''
            }
        },
        mounted(){
            var self = this;

            // 
            var calendar = new Calendar('#demoDate', {
                // 
                minDate: '2017-02-11',

                // 
                maxDate: '2017-05-11',

                // 
                callBack: function (nowVal) {
                    self.nowVal = nowVal;
                }
            });
        },
	}
</script>

<style scoped>
    @import "../../lib/calendar/calendar.css";
    #demoDate {
        width: 100%;
    }
</style>


